import { StyleSheet, View, Text, TouchableOpacity } from 'react-native'
import { useMediaQuery } from 'react-responsive'
import howToOpenSpace from '../assets/howToOpenSpace.pdf'

const Open = () => {
    const isPortrait = useMediaQuery({ orientation: 'portrait' })

    return (
        <View style={styles.container}>
            <Text style={[styles.title, isPortrait ? { margin: 15 } : null]}>  </Text>
            <Text style={[styles.subtitle, isPortrait ? { margin: 15 } : null]}>     ()  ,          .</Text>
            <View style={[styles.infoContainer, { flexDirection: isPortrait ? 'column' : 'row' }, isPortrait ? { margin: 0 } : null]}>
                <View style={[styles.block, isPortrait ? { margin: 15 } : null]}>
                    <Text style={styles.numberTitle}>1 </Text>
                    <Text style={styles.description}>      .</Text>
                </View>
                {isPortrait ? null : <View style={styles.verticalLine} />}
                <View style={[styles.block, isPortrait ? { margin: 15 } : null]}>
                    <Text style={styles.numberTitle}>2 </Text>
                    <Text style={styles.description}>      .</Text>
                </View>
            </View>
            {isPortrait ? null : <View style={styles.horizontalLine} />}
            <View style={[styles.infoContainer, { flexDirection: isPortrait ? 'column' : 'row' }, isPortrait ? { margin: 0 } : null]}>
                <View style={[styles.block, isPortrait ? { margin: 15 } : null]}>
                    <Text style={styles.numberTitle}>3 </Text>
                    <Text style={styles.description}>   .</Text>
                </View>
                {isPortrait ? null : <View style={styles.verticalLine} />}
                <View style={[styles.block, isPortrait ? { margin: 15 } : null]}>
                    <Text style={styles.numberTitle}>4 </Text>
                    <Text style={styles.description}>    .</Text>
                </View>
            </View>
            {isPortrait ? null : <View style={styles.horizontalLine} />}
            <View style={[styles.infoContainer, { flexDirection: isPortrait ? 'column' : 'row' }, isPortrait ? { margin: 0 } : null]}>
                <View style={[styles.block, isPortrait ? { margin: 15 } : null]}>
                    <Text style={styles.numberTitle}>5 </Text>
                    <Text style={styles.description}> .</Text>
                </View>
                {isPortrait ? null : <View style={styles.verticalLine} />}
                <View style={[styles.block, isPortrait ? { margin: 15 } : null]}>
                    <Text style={styles.numberTitle}>6 </Text>
                    <Text style={styles.description}>    .</Text>
                </View>
            </View>
            {isPortrait ? null : <View style={styles.horizontalLine} />}
            <View style={[styles.infoContainer, { flexDirection: isPortrait ? 'column' : 'row' }, isPortrait ? { margin: 0 } : null]}>
                <View style={[styles.block, isPortrait ? { margin: 15 } : null]}>
                    <Text style={styles.numberTitle}>7 </Text>
                    <Text style={styles.description}>   .</Text>
                </View>
                {isPortrait ? null : <View style={styles.verticalLine} />}
                <View style={[styles.block, isPortrait ? { margin: 15 } : null]}>
                    <Text style={styles.numberTitle}>8 </Text>
                    <Text style={styles.description}>      </Text>
                </View>
            </View>
            <TouchableOpacity accessibilityRole='link' href={howToOpenSpace} download='howToOpenSpace' style={styles.button}>
                <Text style={styles.buttonText}></Text>
            </TouchableOpacity>
        </View>
    )
}

const styles = StyleSheet.create({
    title: {
        fontSize: 34,
        fontWeight: '800',
        textTransform: 'uppercase',
        marginBottom: 60,
    },
    container: {
        maxWidth: 1200,
        width: '100%',
        alignSelf: 'center',
        marginTop: 200,
        marginBottom: 50
    },
    subtitle: {
        fontSize: 24,
        fontWeight: '700',
        marginTop: 40,
        textTransform: 'uppercase'
    },
    infoContainer: {
        flexDirection: 'row',
        marginBottom: 40,
        marginTop: 80,
    },
    description: {
        fontSize: 20,
        fontWeight: '500',
    },
    numberTitle: {
        fontSize: 30,
        fontWeight: '700',
        color: '#4a0a52',
        marginBottom: 30
    },
    block: {
        flex: 1,
        marginBottom: 25,
    },
    verticalLine: {
        borderLeftWidth: 1,
        marginHorizontal: 50
    },
    horizontalLine: {
        borderBottomWidth: 1
    },
    button: {
        alignSelf: 'center',
        height: 55,
        backgroundColor: '#4a0a52',
        justifyContent: 'center',
        alignItems: 'center',
        marginTop: 40,
        paddingHorizontal: 25
    },
    buttonText: {
        color: '#fff',
        fontWeight: '700',
        fontSize: 16
    }
})

export default Open