import { StyleSheet, View, Text, useWindowDimensions } from 'react-native'
import { useMediaQuery } from 'react-responsive'

const About = () => {
    const isPortrait = useMediaQuery({ orientation: 'portrait' })
    const { width } = useWindowDimensions()

    return (
        <View style={styles.container}>
            <View style={[styles.paddingContainer, { maxWidth: isPortrait ? width : 1200 }]}>
                <Text style={styles.title}> </Text>
                <Text style={[styles.subtitle, { marginBottom: isPortrait ? 60 : 0 }]}>  "" :</Text>
            </View>
            <View style={[styles.paddingContainer, { maxWidth: isPortrait ? width : 1200 }]}>
                <View style={[styles.infoContainer, { flexDirection: isPortrait ? 'column' : 'row', marginTop: isPortrait ? 0 : 80 }]}>
                    <View style={styles.block}>
                        <Text style={styles.infoTitle}></Text>
                        <Text style={styles.description}>               .</Text>
                    </View>
                    {isPortrait ? null : <View style={styles.verticalLine} />}
                    <View style={styles.block}>
                        <Text style={styles.infoTitle}></Text>
                        <Text style={styles.description}>       .</Text>
                    </View>
                </View>
                {isPortrait ? null : <View style={styles.horizontalLine} />}
                <View style={[styles.infoContainer, { flexDirection: isPortrait ? 'column' : 'row', marginTop: isPortrait ? 0 : 80 }]}>
                    <View style={styles.block}>
                        <Text style={styles.infoTitle}></Text>
                        <Text style={styles.description}>       .</Text>
                    </View>
                    {isPortrait ? null : <View style={styles.verticalLine} />}
                    <View style={styles.block}>
                        <Text style={styles.infoTitle}></Text>
                        <Text style={styles.description}>      - ,    .</Text>
                    </View>
                </View>
            </View>
        </View>
    )
}

const styles = StyleSheet.create({
    container: {
        alignSelf: 'center',
        marginTop: 200,
        marginBottom: 50
    },
    title: {
        fontSize: 34,
        fontWeight: '800',
        textTransform: 'uppercase',
        marginBottom: 60,
        marginHorizontal: 15
    },
    subtitle: {
        fontSize: 30,
        fontWeight: '700',
        marginTop: 40,
        textTransform: 'uppercase',
        marginHorizontal: 15
    },
    paddingContainer: {
        maxWidth: 1200,
        width: '100%',
    },
    infoContainer: {
        flexDirection: 'row',
        marginBottom: 40,
        marginTop: 80
    },
    description: {
        fontSize: 20,
        fontWeight: '500',
    },
    infoTitle: {
        fontSize: 28,
        fontWeight: '700',
        marginBottom: 25
    },
    block: {
        flex: 1,
        marginBottom: 25,
        marginHorizontal: 15
    },
    verticalLine: {
        borderLeftWidth: 1,
        marginHorizontal: 50
    },
    horizontalLine: {
        borderBottomWidth: 1
    },
})

export default About